/**
 * Three Columns of Services Pattern Styles
 */

.pattern-three-columns-of-services {
	padding: 3rem 0;
	background: #751313;
	
	.wp-block-columns {
		gap: 2rem;
	}

	.wp-block-column {
		text-align: center;
		padding: 2rem;
		background: #751313;
		border-radius: 8px;
		transition: transform 0.3s ease, box-shadow 0.3s ease;

		&:hover {
			transform: translateY(-5px);
			box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
		}

		h3 {
			font-size: 1.5rem;
			margin-bottom: 1rem;
			color: var(--wp--preset--color--site-color);
		}

		p {
			font-size: 1rem;
			line-height: 1.6;
			color: #666;
		}
	}
}
